.offer-element {
  padding: 0;
  cursor: pointer;
}
.offer-element:hover .outer .background {
  /*        transform: scale(1.00);*/
}
.offer-element:hover .outer:after {
  background-position: 0 0%;
}
.offer-element .outer {
  width: 100%;
  padding-top: 100%;
  position: relative;
  overflow: hidden;
  margin: auto;
}
.offer-element .outer .icon {
  background: url("../../images/blatt.png") no-repeat;
  height: 2rem;
  width: 2rem;
  background-size: cover;
  margin: 1rem auto;
}
@media only screen and (max-width: 1480px) {
  .offer-element .outer {
    margin: 0 10px;
  }
}
