  .offer-element .outer {
    height: 76.92vw;
    width: 76.92vw;
  }
  .offer-element .outer .inner .icon {
    display: none;
  }
}
.offer-element .outer .background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: all 300ms ease-in-out;
  background-position: center center;
  background-size: cover;
}
.offer-element .outer:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
